.link-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    display: inline;
    margin: 0;
    padding: 0;
  }
  
  .link-button:hover,
  .link-button:focus {
  text-decoration: none;
  }
html, body {
    background-color: #342E37;
    font-family: 'Roboto', sans-serif;
    background-image:
      radial-gradient(#000000 20%, transparent 0),
      radial-gradient(#000000 20%, transparent 0);
    background-size: 30px 30px;
    background-position: 0 0, 15px 15px;
}

img {
    display: block;
    max-width:150px;
    max-height:300px;
    width: auto;
    height: auto;
  }

.match-item {
    font-weight: bold;
    color: green;
}

.match-item i{
    font-weight: normal;
    font-size: 10px
}

#wrapper {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 20px;
    align-items: center;
}

.card {
    background-color: lightgray;
}
/* navbar */
.navbar {
    height: 100px;
    background-color: #342E37;
    font-size: 30px;
}

p{
    margin-left: 5px;
}

h5.card-title.show-my-listings{
    font-size: 20px;
}

.navbar-brand {
    color: #A2D729;
    font-size: 50px;
    padding-right: 15px
}

.nav-item a {
   color: #FAFFFD
}

.nav-item:hover a {
    color: #e22620
}

.active a {
    font-style: italic; 
    color: #3c91e6;
}

/* cards */
.card-body {
    border: 3px solid #342E37;
    border-radius: 5px;
    background-color: #FAFFFD;
    color: #342E37;
    padding: 0
}

p.faq-card {
    font-weight: 500;
    padding-left: 8px;
    padding-right: 12px;
    text-align: justify;
}

h4.faq-card {
    padding-left: 10px;
}

.card-body h5 {
    padding-left: 10px;
    padding-top: 8px;
    font-size: 30px;
    min-height: 30px;
    background-color: #342E37;
    color: #3c91e6;
    margin-bottom: 0;
    padding-bottom: 12px;
}

.card-body hr {
    margin-top: 0;
    margin-bottom: 8px;
    border-top: 1px solid #342E37;
    width: 100%
}

.search-listings {
    padding-left: 10px;
    padding-top: 8px;
    font-size: 14px;
    background-color: #342E37;
    color: #3c91e6;
    margin-bottom: 0;
    margin-right: 0;
    margin-left: 0;
    padding-bottom: 12px;
}
.sign-in-card {
    width: 500px;
}

.sign-in-card p {
    margin-left: 0px;
}

.sign-in-form {
    padding: 6px 10px
}

.card-subtitle {
    padding-left: 10px;
    padding-top: 2px;
    font-size: 10px;
    font-style: italic;
    background-color: #342E37;
    margin-bottom: 0;
    padding-bottom: 2px;
}

.card.result {
    margin-bottom: 6px;   
    color: #342E37;   
     margin-left: 5%;
    margin-right: 5%
}

.result-form {
    padding-left: 5%;
    padding-right: 5%
}
.result-button p {
    margin: 0px;
    white-space: normal;
    padding-left: 5%;
    padding-right: 5%

}


button.cust-btn{
    background-color: #342E37;
    color: #FAFFFD;
    border-color: black;
}

button.review-offer-btn{
    background-color: lightgreen;
    color: black;
    border-color: lightgrey
}

button.review-offer-btn:hover{
    background-color: limegreen;
}

button.cancel-btn{
    background-color:lightcoral;
    color:black;
    border-color:lightgray
}

button.cancel-btn:hover{
    background-color:rgb(235, 78, 78)
}

button.add-inv-btn:hover{
    background-color:black;
    color: #3c91e6
}

button.make-offer-btn:hover {
    background-color: black;
    color: #A2D729;
}

/* Listing Item */
.card#listing-item {
    border: 0;
}
.card-body#listing-item {
    border: 0;
    padding: 10px
}

.card-body#listing-item h5 {
    padding-left: 0;
    padding-top: 0;
    font-size: 20px;
    background-color: #FAFFFD;
    color: #e22620;
    padding-bottom: 8px;
}

.navbar-collapse{
    z-index: 95;
    background: #342E37;
    padding: 10px;
}

span.navbar-toggler-icon {
    color:white;
    z-index: 98;
}

@media only screen and (max-width: 800px){
    body{
        line-height: 1;
    }
    #wrapper{
        padding-left: 2%;
        padding-right: 2%;
    }
    .row{
        margin-left: 0;
        margin-right: 0
    }
    .col-3{
        flex: none;
        max-width: 100%;
        margin-bottom: 15px;
    }
    .col-4{
        flex: none;
        max-width: 100%;
    }
    .col-8{
        flex: none;
        max-width: 100%;
        margin-bottom: 15px;
    }
    .col-6{
        flex: none;
        max-width: 100%;
        margin-bottom: 15px;
    }
    .col-9{
        flex: none;
        max-width: 100%;
        margin-bottom: 15px;
    }
    .sign-in-card{
        max-width: 100%;
    }
}

